import Vue from 'vue'
import VeeValidate ,{ Validator } from 'vee-validate'
import zh_CN from 'vee-validate/dist/locale/zh_CN'
import VueI18n from 'vue-i18n'
import { rejects } from 'assert';

Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'zh_CN',
})
const config = {
  errorBagName: 'errors', 
  fieldsBagName: 'fields',
  delay:100,   
  locale: 'zh_CN', 
  strict: true,  
  enableAutoClasses: true,
  events: 'input|blur', //focus|input|blur|submit
  inject: true
};
Vue.use(VeeValidate,config,{
  i18n,
  i18nRootKey: 'validation',
  dictionary: {
    zh_CN
}})
//自定义规则
const tel = {
  getMessage: function (name) { return " 请输入正确格式的手机号" },
  validate: (value, args) => {
    return value.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value)
  }
}

const password={
  getMessage:function(name){return name+"应为字母、数字和符号两种及以上的组合，6-16个字符"},
  validate:(value, args)=>{
    return /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?![,\!\.#%'\+\*\-:;^_`]+$)[,\!\.#%'\+\*\-:;^_`0-9A-Za-z]{6,16}$/.test(value)
  },
}
const cnLength={
  getMessage:function(name,args){return name+"长度不能超过"+args[0]+"位"},
  validate:(value, args)=>{
    var valLen=0;
    if(typeof value=="number"){
      value+="";
      valLen=value.length;
    }else if(value===undefined){
      return true;
    }
    else{
      valLen=value.replace(/[\u0391-\uFFE5]/g,"aa").length;
    }
    return valLen<=args[0];
  },
}
const idnumber={
  getMessage:function(name,args){return name+"格式不正确"},
  validate:(value, args)=>{
    return /(^\d{110}$)|(^\d{112}$)|(^\d{17}(\d|X|x)$)/.test(value)
  },
}
//内置规则格式 name 代表返回的数据名称，html元素存在data-vv-as时为其中的值，不存在是为name值，args为传入参数，args[0]为自身拥有数据,args[1]为一个对象，具体内部不清楚，args[2]不清楚
//规定内置规则提供信息
VeeValidate.Validator.localize({
  zh_CN: {
    messages: {
      numeric:function(name){return name+"应为数字"},
      required: function (name) { return name + '不能为空'},
      max:function(name,args){
        return name+'不能超过'+args[0]+"个字符"
      },
      min:function(name,args){
        return name+'不能少于'+args[0]+"个字符"
      },
      length:function(name,args){
        return name+'应该为'+args[0]+"个字符"
      },
      regex:function(name){return name+"格式不正确"},
      alpha_num:function(name,args){
        return name+"只能为数字或英文";
      },
      email:function(name){
        return name+"格式不正确";
      }
    }
  }
})
var times;
const validate = (value, args) =>{
  var promise=new Promise((resolve,rejects) => {
    clearTimeout(times)
    if (value) {
      var data = {
        data: {
          data: value,
          name: args[0]
        },
        states: {
          id: args[1]
        }
      };
      var Strurl=args[2]?args[2]+"/inspectData.action":"inspectData.action";
      times=setTimeout(function(){
        Vue.prototype.$axios
          .post(Vue.prototype.HOST + Strurl, data)
          .then(function(success) {
            if (success.bool) {
              resolve(true);
            } else{
              console.log(success)
              resolve(false);
            }
          })
          .catch(function(error) {
            console.log(error);
            resolve(false);
          })
      },800);
    } else {
      return false;
    }
  });
  return promise;
};
const val = {
      getMessage: function(name) {
        return "已存在";
      },
      validate: validate
    };
Validator.extend('tel', tel);
Validator.extend('password', password);
Validator.extend("cnlength",cnLength);
Validator.extend("idnumber",idnumber);
Validator.extend("val", val);



