import axios from 'axios'
import layer from 'layui-layer'
import router from '@/router/index'
import store from '@/store/index'
//http请求拦截器
var codes="500"
axios.interceptors.request.use(config => {
  // element ui Loading方法
  config.headers['X-Request-With']='XMLHttpRequest';
  return config
 }, error => {
  layer.msg("请求超时", {
    icon: 2,
    time: 1000
  });
  return Promise.reject(error);
 })
// http响应拦截器
axios.interceptors.response.use(data => {
  const code = data.data.code;
  var resdata={bool:false,data:{},msg:"",islogin:true};
  switch (code) {
    case "0":
    resdata.bool=true;
    resdata.data=data.data.data;
    codes="0"
    break;
    case "500":
    if(data.data.msg=='已存在'){
    }else{
      layer.msg(data.data.msg, {
        icon: 2,
        time: 1000
      });
    }
      resdata.msg=data.data.msg;
      codes="500"
      break;
    case "-999":
    if(codes!=code){
      resdata.islogin=false;
    layer.alert('帐号未登录', {
      icon: 1,
      time: 2000, //2s后自动关闭
      end: function () {
        store.commit("SET_IS_LOGIN", false);
        store.commit("REMOVE_USER");
        router.push("/");
      }
    } )
    }
    codes="-999"
      break;
  }
  return resdata;
}, error => {
    layer.alert("网络错误，请检查网络是否畅通", {
        icon: 5,
        time: 2000
      });
  return Promise.reject(error);
})

export default axios;



