import {
  resolve
} from "path";

import vm from '../main'

const http = {
  loginleftmenu() {
    vm.$axios
      .post(vm.HOST + "selectUserPage.action")
      // .get("static/json/menu.json")
      .then(function (response) {
        var res = response;
        if (res.bool) {
          var menus = vm.$api.opreate.addShow(res.data.userPageList);
          vm.$store.commit("UPDATE_USERPAGE", menus);
          vm.$store.commit("UPDATE_USERPERMS", res.data.perms);
        }
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  loginTableData(action, states) {
    vm.$store.commit("REMOVECHECK");
    var pages = vm.$store.getters.GETUSERTABLEPAGE;
    var data = {
      page: {
        active: pages.active,
        pagelist: pages.pagelist
      },
      state: states
    }
    var m = layer.msg("数据加载中", {
      icon: 16,
      shade: [0.1, '#ccc'] //0.1透明度的白色背景
    });
    vm.$axios
      .post(vm.HOST + action, data)
      .then(function (success) {
        var res = success;
        if (res.bool) {
          vm.$store.commit("UPDATETITLEDATA", judgeTitle(res.data.data_title));
          vm.$store.commit("UPDATELISTTOTAL", res.data.listTotal);
          vm.$store.commit("UPDATEDATAS", res.data.lists);
          vm.$store.commit("UPDATEPAGETOTAL", Math.ceil(pages.listTotal / pages.pagelist));
          vm.$store.commit("UPDATEPAGES", pagelogin(
            pages.pageTotal,
            pages.active,
            pages.count
          ));
          vm.$store.commit("UPDATECHECKDATA", []);
          layer.close(m);
        } else {
         if(res.islogin){
          m = layer.msg("获取失败", {
            icon: 5
          });
         }
        }
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  //valBool是否跳过全部表单验证，默认不跳过
  opreateHttp(url, data, opreatetitle,valBool=false) {
    var tableSelectData=vm.$store.getters.GETTABLESELECTDATA;
    var bool = {
      bool: false
    };
    var httpres=()=>{
      return vm.$axios
              .post(vm.HOST + url, data)
              .then(function (success) {
                  if(success.bool){
                    vm.$layer.msg(opreatetitle +"成功", {icon: 1, time: 800,end: function () {
                      vm.$store.commit("UPDATEPAGEACTIVE",1);
                      http.loginTableData( tableSelectData.selectTableurl, tableSelectData.pageloginData);
                        }
                      });
                    bool.bool = true;
                    data = {};
                  }else{
                    bool.bool=false;
                  }
                  return bool.bool;
              })
              .catch(function (error) {
                console.log(error)
                bool.bool = false;
                return bool.bool;
          })
    }
    var parse=new Promise((function(resolve, reject){
      //做一些异步操作
      resolve(false)
    }));
    if(vm.$validator.errors.items.length==0){
      if(valBool){
        parse=httpres();
      }else{
        parse=vm.$validator.validateAll().then(function (value) {
          if (value) {
            return httpres();
          }
        }).catch(error => {
          console.log(error)
          bool.bool = false;
          return bool.bool;
        });
      }
    }else{
      parse=false;
      layer.msg("请消除输入错误后提交", {
        icon: 5
      });
    }
    return parse;
  },

}
export default http;



//过滤表格title
function judgeTitle(data) {
  var datas = data;
  if (datas.length) {
    for (var i = 0; i < datas.length; i++) {
      if (datas[i] == "密码") {
        datas = datas.splice(i, 1);
      }
    }
    return datas;
  } else {
    return [];
  }
}

function datafilter(value, type) {
  //为小于10的数字加0
  function addZero(val) {
    if (val < 10) {
      return "0" + val;
    } else {
      return val;
    }
  }
  var date = new Date(Number(value));
  var year = addZero(date.getFullYear());
  var month = addZero(date.getMonth() + 1);
  var day = addZero(date.getDate());
  if (type == "yyyyMMdd") {
    return year + "年" + month + "月" + day + "日";
  }
}
//生成页数
function pagelogin(total, active, count) {
  var from = active - Math.floor(count / 2); //最左侧页码
  var to = active + Math.floor(count / 2); //最右侧页码
  if (from <= 0) {
    from = 1;
    to = from + count - 1;
  }
  if (to > total) {
    to = total;
    from = to - count + 1;
  }
  var range = [];
  for (var i = from; i <= to; i++) {
    if (i > 0) {
      range.push(i);
    }
  }
  if (range.length > 0) {
    range.push("»");
    range.unshift("«");
    range.push("尾页");
    range.unshift("首页");
  }
  var pages = range;
  return pages;
}



