import Vue from 'vue'
import Router from 'vue-router'
// console.log(process.env.NODE_ENV)
const _import = require('./_import_production');// + process.env.NODE_ENV
Vue.use(Router)
const routerDefaultMap= [
  {
    path: '/',
    name: 'Login',
    component: _import('Login'),
    meta:{
      title:"登录"
    }
  },
  {
    path: '/rigster',
    name: 'rigster',
    component:_import('rigster'),
    meta:{
      title:"注册"
    }
  },
  {
    path: '/main',
    name: 'main',
    component:_import('main'),
    meta:{
      requireAuth:true
    },
    children:[
      {
        path:"/main/gongnengguanli",
        name:"gongnengguanli",
        component:_import("Administration/gongnengguanli"),
        meta:{
          title:'功能',
          requireAuth:true
        }
      },
      {
        path:"/main/jueseguanli",
        name:"jueseguanli",
        component:_import("Administration/jueseguanli"),     
        meta:{
          title:'角色',
          requireAuth:true
        }
      },
      {
        path:"/main/yonghuguanli",
        name:"yonghuguanli",
        component:_import("Administration/yonghuguanli"),
        meta:{
          title:'用户',
          requireAuth:true
        }
      },
      {
        path:"/main/applicationMan",
        name:"applicationguanli",
        component:_import("Administration/application"),
        meta:{
          title:'应用系统',
          requireAuth:true
        }
      },
      {
        path:"/main/changjingleibie",
        name:"changjingleibie",
        component:_import("deviceManager/sceneManager"),
        meta:{
          title:'设备场景类别',
          requireAuth:true
        }
      },
      {
        path:"/main/changjingdizhi",
        name:"changjingdizhi",
        component:_import("deviceManager/sceneAddress"),
        meta:{
          title:'设备场景地址',
          requireAuth:true
        }
      },
      {
        path:"/main/device_deploy_node",
        name:"device_deploy_node",
        component:_import("deviceManager/deploy_node"),
        meta:{
          title:'设备部署节点',
          requireAuth:true
        }
      },
      {
        path:"/main/device_control_type",
        name:"device_control_type",
        component:_import("deviceManager/control_type"),
        meta:{
          title:'设备控制类型',
          requireAuth:true
        }
      },
      {
        path:"/main/device_manufacturer",
        name:"device_manufacturer",
        component:_import("deviceManager/manufacturer"),
        meta:{
          title:'设备厂商信息',
          requireAuth:true
        }
      },
      {
        path:"/main/device_equip_type",
        name:"device_equipment_type",
        component:_import("deviceManager/equipment_type"),
        meta:{
          title:'设备类型信息',
          requireAuth:true
        }
      },
      {
        path:"/main/equipmentinfor",
        name:"equipmentinfor",
        component:_import("deviceManager/equipmentinfor"),
        meta:{
          title:'设备类型信息',
          requireAuth:true
        }
      },
      {
        path:"/main/sensorTypeInfo",
        name:"sensorTypeInfo",
        component:_import("IntelligentAgriculture/sensorTypeInfo"),
        meta:{
          title:"传感器类型管理",
          requireAuth:true
        }
      },
      {
        path:"/main/realTimeMonitor",
        name:"realTimeMonitor",
        component:_import("IntelligentAgriculture/realTimeMonitor"),
        meta:{
          title:"实时监测",
          requireAuth:true
        }
      },
      {
        path:"/main/soliInfoManagement",
        name:"soliInfoManagement",
        component:_import("IntelligentAgriculture/soliInfoManagement"),
        mata:{
          title:"土壤信息管理",
        }
      },
      {
        path:"/main/nodeEquipmentM",
        name:"nodeEquipmentM",
        component:_import("IntelligentAgriculture/nodeEquipmentM"),
        meta:{
          title:"节点设备",
          requireAuth:true
        }
      },
      {
        path:"/main/cropInfoManagement",
        name:"cropInfoManagement",
        component:_import("IntelligentAgriculture/cropInfoManagement"),
        meta:{
          title:"植物信息管理",
          requireAuth:true
        }
      },
      {
        path:"/main/cropVarietyInfoM",
        name:"cropVarietyInfoM",
        component:_import("IntelligentAgriculture/cropVarietyInfoM"),
        meta:{
          title:"作物品种信息管理",
          requireAuth:true
        }
      },
      {
        path:"/main/plantInfo",
        name:"plantInfo",
        component:_import("IntelligentAgriculture/plantInfo"),
        meta:{
          title:"种植信息管理",
          requireAuth:true
        }
      },
      {
        path:"/main/disposeMethod",
        name:"disposeMethod",
        component:_import("IntelligentAgriculture/disposeMethod"),
        meta:{
          title:"处理方法信息",
          requireAuth:true
        }
      },
      {
        path:"/main/alertInfo",
        name:"alertInfo",
        component:_import("IntelligentAgriculture/alertInfo"),
        meta:{
          title:"报警信息信息",
          requireAuth:true
        }
      }
    ]
  },
  {
    path:"/userindex",
    name:"userindex",
    component:_import('user/users'),
    meta: {
      // 添加该字段，表示进入这个路由是需要登录的
       requireAuth: true,  
      },
  }
];
var router= new Router({
  // base: __dirname,
  routes:routerDefaultMap
})

export default router;



